import React from "react"
import Layout from "../components/layout";

import image1 from "../images/BusyBack1.png"
import image2 from "../images/BusyBack2.png"
import image3 from "../images/BusyBack3.png"
import image4 from "../images/BusyBack4.png"
import image5 from "../images/BusyBack5.png"

var visible = false;

const buttonClick = () => {
  console.log("Button clicked");
  var images = document.getElementById("more-mobile-app-photos");
  var button = document.getElementById("show-more-button");

  if(!visible) {
    visible = true;
    images.style.display = "flex";
    button.innerHTML = "Show Less";
  } else {
    visible = false;
    images.style.display = "none";
    button.innerHTML = "Show More";
  }
}

const BusyBackpack = function() {
  return (
    <Layout>
      <h1 className="projectHeading">Busy Backpack</h1>
      <div id="projects-images">
        <figure>
          <img src={image1} alt="Booking System" 
                            id="busyBackpackImage"
                            title="Busy Backpack home page"/>
           <figcaption>Home page in English (first prototype).</figcaption>
        </figure>
        <figure>
          <img src={image5} alt="Booking System" 
                            id="busyBackpackImage2"
                            title="All events page"/>
          <figcaption>Navigation menu for mobile devices (same design for both prototypes). 
          </figcaption>
        </figure>
        <div id="more-mobile-app-photos">
          <figure>
            <img src={image2} alt="Booking System" 
                              id="busyBackpackImage"
                              title="Busy Backpack home page"/>
            <figcaption>Home page in Chinese (first prototype).</figcaption>
          </figure>
          <figure>
            <img src={image3} alt="Booking System" 
                              id="busyBackpackImage"
                              title="Contact page with login open"/>
            <figcaption>Bootleg log in for the user on the Contact page, due to the database being lost, it has no functionality (first prototype). </figcaption>
          </figure>
          <figure>
            <img src={image4} alt="Booking System" 
                              id="busyBackpackImage"
                              title="All events page"/>
            <figcaption>Events page, this was a calender where event "cards" would sit for each day. A user would click on a card to go to a detail
              event page (second prototype). 
            </figcaption>
          </figure>
        </div>
        <button className="show-more-button" onClick={buttonClick} id="show-more-button">
          Show More
        </button>
      </div>
      <div className="projectInfo">
        <div className="projectInfoCol1">
          <h2>Overview</h2>
          <p>
          During University i did some development internships for Busy Backpack, producing some website prototypes using technologies such as React, NPM, HTML5, and OO-PHP.
          </p>
          <p>
            Their current website was very basic, produced by a website builder, and did not contain the features my clients wanted. 
            Their website had no membership capabilities, user login/sign-up capabilities, multilingual support, and no area where members could share their creative ideas. 
            They wanted these features because it was what they wanted originally and were disappointed to miss out. This is where i presented this prototype to show that it was possible.
          </p>
          <p>
            Effectively what i produced were two prototypes, one made in React and had no application backend, where the other prototype was made using HTML and did have a PHP backend with a database. 
            I was new to React at the time so i did not know how to get PHP to work with React.
            Both prototypes followed the design set in the existing website and added the extra features on top.
          </p>
          <p>
            The prototype produced in HTML and OOPHP showed how the membership sign-up and log-in could work. Users would sign in to the site and when looking at events, they shown two buttons,
            if they were interested in the event, or intend on going, and in the admin page, the admin could see a list of how many clicked like or interested and which are the more popular events.
            This prototype also featured changing the page language through a link in the top right of the page. This worked by loading a .ini file that contained key-value pairs for each translation, while in the PHP code, the .ini file would be loaded,
            then each sentence would be set. This did have the disadvantage of requiring the page to be reload each time the language was changed. Unfortunately, due to the database being lost, most of the UI and functionality is gone.
          </p>
          <p>
            The second prototype made use of React to build the UI, this worked much better since it did not rely on PHP nor did it require the website to be reloaded each time the language was changed. Instead, a JSON file contained the key-value pairs and the i8N module was imported in NPM to load the JSON. 
            There was no database for this prototype during the time it was developed.
          </p>
          <p>
            We also had to think also think about UI and UX for the designs, and making sure the website was visible to search engines for a good SEO index rank.
            I also aimed to make sure that the site looked and fucntioned the same on a mobile device. Finally we looked at how to host the website. It was decided that we would set up a web server using NginX and Google cloud.
          </p>
          <p>
            Unfortunately, the project was never picked up. While the clients was very excited when the prototypes presented, 
            internal difficulties meant this project was not given the go ahead.
          </p>
        </div>
        <div className="projectInfoCol2">
          <p>Date: 2019 - 2020</p>
        </div>
      </div>
    </Layout>
  );
}

export default BusyBackpack;